<template>
  <div class="container" @click='handleGallaryClose'>
    <div class="wrapper">
      <swiper :options="swiperOption">
        <!-- slides -->
        <swiper-slide
        v-for='(item,index) of gallaryImgs'
        :key='index'
        >
          <img class='gallary-img' :src="item">
      	</swiper-slide>
          <!-- Optional controls -->
          <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>


<script>
export default {
  name: 'CommonGallary',
  data (){
  	return {
  		swiperOption: {
        	pagination: '.swiper-pagination',
        	paginationType: 'fraction',
        	observer:true,
        	observeParents:true
      }
  	}
  },
  props:{
  	gallaryImgs:{
  		type:Array,
  		default (){
  			return []
  		}
  	}
  },
  methods:{
  	handleGallaryClose (){
  		this.$emit('close')
  	}
  }
};
</script>
<style lang="stylus" scoped>
.container >>> .swiper-container
	overflow:inherit
.container
	display:flex
	flex-direction:column
	justify-content:center
	position:fixed
	left:0
	right:0
	top:0
	bottom:0
	z-index:99
	background:#000
	.wrapper
		width:100%
		.gallary-img
			width:100%
		.swiper-pagination
			color:#fff
			bottom:-1rem
</style>



